<template>
  <ion-page>
    <Back :btnSrc="()=>router.push('profile')"/>
    <Layout
      height="false"
      :method="editProfileHandler"
      :method2="
        () => {
          router.push('/profileSend');
        }
      "
      :loading="loading"
      :filledBtn="'Сохранить'"
      title="Личный кабинет"
      :outlineBtn="'Изменить пароль'"
    >
      <template v-slot:main-content>
        <ion-text>
          <p class="title ion-text-start">Мои данные</p>
        </ion-text>
        <div>
          <ion-text><p class="sub-title">Имя</p> </ion-text>
          <Input
            @updated="(item) => (name = item)"
            :changeHandler="nameChange"
            :value="name"
            name="Укажите имя"
          />
          
          <ion-text>
            <p class="sub-title">{{ data.email }}</p>
          </ion-text>
          <Input
            name="Электронная почта"
            :value="email"
            :changeHandler="emailChange"
          />
          <ion-item>
            <ion-text>
              <p class="sub-title">{{ login }}</p>
              <p>Контактный телефон</p>
            </ion-text>
          </ion-item>
        </div>
        <ion-text v-if="profileError?.response?.data?.error" class="error">
          {{ profileError?.response?.data?.error }}
        </ion-text>
      </template>
    </Layout>
  </ion-page>
</template>


<script>
import { defineComponent, ref } from "vue";
import { useRouter } from "vue-router";
import Layout from "../components/Layout.vue";
import {
  onIonViewDidEnter,
  IonPage,
  IonText,
  IonItem,
} from "@ionic/vue";
import Input from "../components/Input.vue";
import Back from "../components/Back.vue";
import { storeToRefs } from "pinia";
import { useProfileStore } from "../stores/profile";
import { Storage } from "@ionic/storage";

export default defineComponent({
  name: "profleEditPage",
  data() {
    return {
      codeSent: false,
      edit: false,
      data: {
        name: "Иван",
        surname: "Иванов",
        secondname: "Иванович",

        email: "Pochta@mail.ru",
        phone: "+7(996)-915-88-31",
      },
    };
  },
  components: {
    Input,
    Back,
    IonPage,
    Layout,
    IonItem,
    IonText,
  },
  setup() {
    const router = useRouter();
    const { profileResponse, profileError } = storeToRefs(useProfileStore());
    const { editProfile } = useProfileStore();
    let name = ref("");
    const surname = ref("");
    const email = ref("");
    const login = ref("");
    let profileData = null;
    let profileDataParsed = null;
    let loading = ref(false)
    const storageHandler = async () => {
      const store = new Storage();
      await store.create();
      profileData = await store.get("token");
      profileDataParsed = JSON.parse(profileData);
      name.value = profileDataParsed?.name;
      /* surname.value = profileDataParsed?.lastName; */
      email.value = profileDataParsed?.email;
      login.value = profileDataParsed?.phone;
    };

    const editProfileHandler = async () => {
      loading.value = true
      editProfile(JSON.parse(profileData)?.token, name.value, email.value).then(
        async () => {
          loading.value = false
          const store = new Storage();
          await store.create();
          const data = {
            ...profileDataParsed,
            name: name.value,
            email: email.value,
          };
          await store.set("token", JSON.stringify(data))
        }
      );
    };

    const nameChange = (e) => {
      name.value = e.target.value;
    };
    const surnameChange = (e) => {
      surname.value = e.target.value;
    };
    const emailChange = (e) => {
      email.value = e.target.value;
    };

    onIonViewDidEnter(() => {
      storageHandler();
    });
    return {
      loading,
      profileResponse,
      profileError,
      router,
      editProfileHandler,
      nameChange,
      surnameChange,
      emailChange,
      surname,
      name,
      email,
      login,
    };
  },
});
</script>

<style scoped>
ion-item {
  --padding-start: 0;
  --padding-bottom: 0px;
  --inner-padding-bottom: 0px;
}
</style>